/* Menu.css - Новый дизайн как на скрине 2 */

/* === ВЕРХНЯЯ НАВИГАЦИЯ КАТЕГОРИЙ (как на Winners) === */
.category-nav {
  background: white;
  border-bottom: 1px solid #f0f0f0;
  padding: 16px 0;
  position: sticky;
  top: 70px;
  z-index: 90;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
}

.category-nav-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  gap: 8px;
  overflow-x: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.category-nav-container::-webkit-scrollbar {
  display: none;
}

.category-nav-btn {
  padding: 10px 24px;
  background: #f8f8f8;
  border: 1px solid #e8e8e8;
  border-radius: 25px;
  color: #666;
  font-size: 15px;
  font-weight: 500;
  white-space: nowrap;
  cursor: pointer;
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.category-nav-btn:hover {
  background: #f0f0f0;
  transform: translateY(-2px);
}

.category-nav-btn.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-color: transparent;
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

/* === ГЛАВНОЕ МЕНЮ === */
.menu-main-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 30px 20px;
}

.menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.menu-header h1 {
  font-size: 32px;
  color: #1a1a1a;
  margin: 0;
}

.search-box {
  position: relative;
  width: 300px;
}

.search-box input {
  width: 100%;
  padding: 14px 18px 14px 48px;
  border: 2px solid #e8e8e8;
  border-radius: 12px;
  font-size: 16px;
  background: #fafafa;
  transition: all 0.3s ease;
}

.search-box input:focus {
  outline: none;
  border-color: #667eea;
  background: white;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.search-icon {
  position: absolute;
  left: 18px;
  top: 50%;
  transform: translateY(-50%);
  color: #999;
  font-size: 18px;
}

/* === СЕТКА ТОВАРОВ === */
.products-grid-new {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 24px;
  margin-top: 20px;
}

.product-card-new {
  background: white;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.06);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.1);
  border: 1px solid #f0f0f0;
  position: relative;
}

.product-card-new:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.12);
  border-color: #e0e0e0;
}

.product-image-new {
  height: 200px;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.product-image-new img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.6s ease;
}

.product-card-new:hover .product-image-new img {
  transform: scale(1.08);
}

.badge-popular {
  position: absolute;
  top: 12px;
  left: 12px;
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  color: white;
  padding: 6px 14px;
  border-radius: 20px;
  font-size: 13px;
  font-weight: 600;
  box-shadow: 0 4px 8px rgba(245, 158, 11, 0.3);
  z-index: 2;
}

.product-info-new {
  padding: 20px;
}

.product-header-new {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 12px;
}

.product-name-new {
  font-size: 20px;
  font-weight: 700;
  color: #1a1a1a;
  margin: 0;
  line-height: 1.3;
}

.product-price-new {
  font-size: 24px;
  font-weight: 800;
  color: #10b981;
  margin: 0;
}

.product-description-new {
  color: #666;
  font-size: 14px;
  line-height: 1.5;
  margin-bottom: 16px;
  min-height: 42px;
}

.product-category-new {
  margin-bottom: 20px;
}

.category-tag-new {
  display: inline-block;
  background: #e0e7ff;
  color: #4f46e5;
  padding: 6px 14px;
  border-radius: 20px;
  font-size: 13px;
  font-weight: 600;
}

.add-to-cart-btn-new {
  width: 100%;
  padding: 14px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.add-to-cart-btn-new:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.4);
}

.add-to-cart-btn-new:active {
  transform: translateY(0);
}

.add-to-cart-btn-new:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none !important;
}

.cart-icon-btn {
  font-size: 18px;
}

/* === МОБИЛЬНАЯ ВЕРСИЯ === */
@media (max-width: 768px) {
  .category-nav {
    top: 60px;
    padding: 12px 0;
  }
  
  .category-nav-container {
    padding: 0 15px;
    gap: 6px;
  }
  
  .category-nav-btn {
    padding: 8px 18px;
    font-size: 14px;
  }
  
  .menu-main-container {
    padding: 20px 15px;
  }
  
  .menu-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }
  
  .menu-header h1 {
    font-size: 28px;
  }
  
  .search-box {
    width: 100%;
  }
  
  .products-grid-new {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .products-grid-new {
    grid-template-columns: 1fr;
  }
  
  .product-card-new {
    border-radius: 12px;
  }
  
  .product-price-new {
    font-size: 22px;
  }
}

/* Кнопки в карточке товара */
.product-buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.quick-add-btn {
  background: linear-gradient(135deg, #4f46e5 0%, #7c3aed 100%) !important;
}

.customize-btn {
  width: 100%;
  padding: 12px;
  background: transparent;
  border: 2px solid #4f46e5;
  color: #4f46e5;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.customize-btn:hover {
  background: #4f46e5;
  color: white;
}

/* Адаптивность для кнопок */
@media (max-width: 480px) {
  .product-buttons {
    flex-direction: column;
  }
  
  .customize-btn {
    padding: 10px;
    font-size: 13px;
  }
}

/* === ИСПРАВЛЕНИЕ ДЛЯ МОБИЛЬНОЙ ВЕРСИИ === */

/* На мобильных устройствах (до 768px) показываем 2 колонки */
@media (max-width: 768px) {
  .products-grid-new {
    grid-template-columns: repeat(2, 1fr) !important;
    gap: 16px !important;
  }
  
  /* Уменьшаем отступы для карточек */
  .product-card-new {
    margin: 0 !important;
    padding: 0 !important;
  }
  
  .product-info-new {
    padding: 15px !important;
  }
  
  .product-name-new {
    font-size: 16px !important;
    margin-bottom: 6px !important;
  }
  
  .product-price-new {
    font-size: 20px !important;
  }
  
  .product-description-new {
    font-size: 12px !important;
    line-height: 1.4 !important;
    margin-bottom: 10px !important;
    display: -webkit-box !important;
    -webkit-line-clamp: 2 !important;
    -webkit-box-orient: vertical !important;
    overflow: hidden !important;
    min-height: 34px !important;
  }
  
  .badge-popular {
    font-size: 10px !important;
    padding: 4px 8px !important;
    top: 8px !important;
    left: 8px !important;
  }
  
  .product-buttons {
    gap: 8px !important;
  }
  
  .add-to-cart-btn-new {
    padding: 10px !important;
    font-size: 13px !important;
  }
  
  .customize-btn {
    padding: 8px !important;
    font-size: 12px !important;
    border-width: 1px !important;
  }
}

/* На очень маленьких экранах (до 480px) немного уменьшаем */
@media (max-width: 480px) {
  .products-grid-new {
    grid-template-columns: repeat(2, 1fr) !important;
    gap: 12px !important;
  }
  
  .product-image-new {
    height: 150px !important;
  }
  
  .product-info-new {
    padding: 12px !important;
  }
  
  .product-name-new {
    font-size: 14px !important;
  }
  
  .product-price-new {
    font-size: 18px !important;
  }
  
  .product-description-new {
    font-size: 11px !important;
    -webkit-line-clamp: 2 !important;
  }
  
  .category-tag-new {
    font-size: 11px !important;
    padding: 4px 8px !important;
  }
}

/* На совсем маленьких экранах (менее 360px) */
@media (max-width: 360px) {
  .products-grid-new {
    grid-template-columns: 1fr !important; /* Возвращаем 1 колонку */
  }
}

/* Убираем отступы у родительских контейнеров */
@media (max-width: 768px) {
  .menu-main-container {
    padding: 15px !important;
  }
  
  .category-nav-container {
    padding: 0 10px !important;
  }
}
